.courses-advantages {
    display: flex;
    flex-wrap: wrap;
    max-width: 482px;
    justify-content: space-between;
    margin-left: auto;
}

.courses-advantages__item {
    background-image: url(../img/tick.png);
    background-repeat: no-repeat;
    background-position: -11px -3px;
    max-width: 300px;
    margin-bottom: 41px;
    font-size: 14px;
    line-height: 20px;
    padding-left: 45px;
    min-height: 40px;

    @include media-breakpoint-up(md) {
        margin-right: 0px;
        padding-top: 48px;
        max-width: 220px;
        padding-left: 0;

    }
}
