.main-navigation {
    position: fixed;
    top: 0;
    left: -200%;
    width: 100%;
    height: 100vh;
    max-width: 600px;
    z-index: 9;
    background: #fefaf9;
    overflow: auto;
    padding-top: 65px;
    padding-bottom: 40px;
    transition: 1s;
    padding-left: 150px;
}

.main-navigation.active {
    left: 0;
}

.main-navigation__link {
    display: block;
    margin-bottom: 15px;
    font: 400 16px 'Oranienbaum', sans-serif;
    text-decoration: none;
    color: $black-color;
}


