.contacts {
    display: flex;
    
    flex-wrap: wrap;
    padding-left: 0px;
    margin: auto;
    width: 100%;
    

    @include media-breakpoint-up(sm) {
        // padding-left: 81px;
        padding-left: 87px;
        padding-right: 87px;
        margin-bottom: 43px;
    }

    @media (min-width: 400px){
        justify-content: space-between;
    }
}

.contacts__block {
    max-width: 160px;
    margin-bottom: 32px;
    min-width: 112px;
    color: $white-color;

    @include media-breakpoint-up(sm) {
        // min-width: 185px;
        // max-width: 256px;
        margin-bottom: 40px;
        max-width: 200px;
    }

    &:nth-child(2) {
        padding-left: 23px;

        @media (min-width: 400px){
            padding-left: 0px;
        }
    }

    &:nth-child(4) {
        padding-left: 72px;

        @media (min-width: 400px){
            padding-left: 0px;
        }
    }
}


.contact__title {
    font-size: 18px;
    font-family: "Oranienbaum";
    margin-bottom: 21px;
    padding-bottom: 17px;
    font-weight: 300;
    position: relative;



    @include media-breakpoint-up(md) {
        font-size: 26px;
        margin-bottom: 34px;
    }

    &:after {
        background-color: #889971;
        content: "";
        position: absolute;
        width: 31px;
        height: 1px;
        bottom: 2px;
        left: 0;
    }
}

.contacts__item {
    font-size: 10px;
    margin-bottom: 5px;
    line-height: 17px;
    letter-spacing: 0.1px;


    @include media-breakpoint-up(md) {
        font-size: 14px;
        margin-bottom: 10px;
        line-height: 22px;
    }

}

.contacts__item a {
    color: $white-color;
}
