.modal-city {
    background: linear-gradient(116deg, #fafcfc,#ffe6c2);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    z-index: 99999;
    height: 100%;
    min-height: 100%;
    -webkit-overflow-scrolling: touch;
    display: none;
}

.modal-city.active {
    display: block;
}

.modal-city__container {
    @include make-container();

}

.modal-city__logo {
    max-width: 250px;
    margin: 0 auto;
    display: block;
}

.modal-city__title {
    position: relative;
    font-family: 'Oranienbaum';
    font-size: 38px;
    font-weight: 500;
    color: $black-color;
}

.modal-city .closed-modal {
    width: 16px;
    height: 16px;
    position: absolute;
    right: 2.5%;
    top: 50%;
    margin-top: -8px;
    cursor: pointer;
}

.closed-modal span {
    display: inline-block;
    width: 1px;
    height: 16px;
    background: #20201d;
    position: absolute;
    left: 8px;
    transform: rotate(45deg);

    &:nth-child(2) {
        transform: rotate(-45deg);
    }
}

.modal-city__list {
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    align-content: baseline;
    flex-direction: row;
    height: auto;
    
    @include media-breakpoint-up(xl) {
        flex-direction: column;
        height: 100vh;
    }
}

.modal-city__item {
    width: 16%;
    min-width: 145px;

    @include media-breakpoint-up(md) {
        min-width: 200px;
    }
}

.modal-city__item-title {
    font-family: 'Oranienbaum';
    font-size: 40px;
    font-weight: 500;
    line-height: 70px;
    margin-bottom: 10px;
    margin-top: 32px;
}

.modal-city__item li {
    margin-bottom: 7px;
}

.modal-city__item a {
    font-size: 18px;
    color: #666666;
}

.modal-city__footer {
    height: 100px;
    display: flex;
    align-items: center;
    position: relative;
    flex-direction: column;
    justify-content: center;

    @include media-breakpoint-up(md) {
        flex-direction: row;
        justify-content: space-between;
    }
}

.modal-city__policy {
    font-size: 10px;
    color: $black-color;
    text-decoration: underline;
    order: 2;
}

.modal-city__dev-logo {
    margin-bottom: 10px;

    @include media-breakpoint-up(md) {
        position: absolute;
        left: calc(50% - 60px);
        margin-bottom: 0px;
    }
}

.modal-city__dev-logo img {
    filter: invert(60%);
}
