.services {
    @include make-row();
}

.service {
    margin-bottom: 30px;
    @include make-col-ready();

    @include media-breakpoint-up(sm) {
        @include make-col(7);
        justify-content: unset;
    }

    @include media-breakpoint-up(md) {
        @include make-col(6);
        
    margin-bottom: 40px;
    }

    @include media-breakpoint-up(lg) {
        @include make-col(4);
    }
}

.service__number {
    font-size: 34px;
    font-family: "Oranienbaum";
    color: $light-orange-color;
    padding-left: 0px;

    @include media-breakpoint-up(md) {
        font-size: 42px;
        padding-left: 1px;
    }
}

.service__title {
    font-size: 16px;
    height: 42px;
    line-height: 41px;
    padding-left: 0px;
    letter-spacing: 0.2px;
    

    @include media-breakpoint-up(md) {
        height: 52px;
        line-height: inherit;
        font-size: 20px;
        letter-spacing: initial;
    }

    @include media-breakpoint-up(xl) {
        height: 52px;
        line-height: 48px;
        font-size: 20px;
        letter-spacing: initial;
    }
    
}

.service__description {
    font-size: 14px;
    line-height: 21px;
    height: 82px;
    padding-left: 0px;
    letter-spacing: -0.5px;

    @include media-breakpoint-up(md) {
        padding-left: 4px;
        font-size: 16px;
        line-height: 25px;
        height: 100px;
        letter-spacing: initial;
    }
}

.service__btn {
    width: 191px;

    @include media-breakpoint-up(md) {
        width: 242px;
    }
}