.instagram-slider {
    overflow: unset;

}

.instagram-slider .slick-slide:not(.slick-active) {
    opacity: 0.5;

    @media (min-width: 576px){
        opacity: 1;
    }
    @media (min-width: 768px){
        opacity: 0.5;
    }
    @media (min-width: 1024px){
        opacity: 1;
    }
    @media (min-width: 1240px){
        opacity: 0.5;
    }
}

.instagram-slider .slick-slide {
    // display: flex !important;
    // justify-content: center !important;
    opacity: 1;
}

.instagram-slider__item {
    position: relative;
    margin: 15px 10px;

    &:hover {
        .instagram-slider__hover {
            // display: block;
            opacity: 1;
        }
    }
}

.instagram-slider .slick-arrow {
    width: 50px;
    height: 50px;
    border: none;
    cursor: pointer;
    box-shadow: 0 13px 13px 0 rgba(#000000, 0.26);
    position: relative;
    background-color: $white-color;
    border-radius: 50%;
    z-index: 9;
    position: absolute;

    &:before {
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 35%;
    }

    &:hover, &:focus {
        background-color: $white-color;
    }
}

.instagram-slider .slick-next {
    right: 20.3%;
    top: calc(50% - 15px);

    &:before {
        background-image: url(../img/arrow-left.svg);
        transform: rotate(180deg);
    }
}

.instagram-slider .slick-prev {
    left: 20.3%;
    top: calc(50% - 15px);

    &:before {
        background-image: url(../img/arrow-left.svg);

    }
}

.instagram-slider__image {
    width: 200px;
    height: 200px;
    object-fit: cover;
    box-shadow: 0 9px 27px 0 rgba(#000000, 0.21);
}

.instagram-slider__hover {
    position: absolute;
    top: 0;
    left: calc(50% - 100px);
    width: 200px;
    height: 200px;
    // display: none;
    z-index: 9;
    // opacity: 0.87;
    opacity: 0;
    transition: 0.3s;
    background: linear-gradient(-58deg,rgba(#ff9900, 0.87%),rgba(#ffd89e,0.87%));

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 200px;
        height: 200px;
        background-image: url(../img/instagram-hover.svg);
        background-repeat: no-repeat;
        background-size: 20px;
        background-position: 15%;
    }
    &::before {
        content: "@slimlab_ufa";
        position: absolute;
        top: 45%;
        left: 30%;
        color: #fff;
    }
}
