.reviews {
    background-image: url(../img/reviews-bcgr.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding-top: 88px;
}

.reviews__container {
    @include make-container();

    @include media-breakpoint-up(sm) {
        max-width: $container-sm;
    }

    @include media-breakpoint-up(md) {
        max-width: $container-md;
    }

    @include media-breakpoint-up(lg) {
        max-width: $container-lg;
    }

    @include media-breakpoint-up(xl) {
        max-width: $container-xl;
    }
    position: relative;
}

.reviews__item {
    // height: 200px;
    position: relative;
}

.reviews__item img {
    width: 100%;
    height: 220px;
    object-fit: cover;
}

.reviews__slider {
    // margin-top: 100px;
    position: relative;
}

.reviews__slider .slick-slide {
    opacity: 0.5;
    overflow: hidden;
    border-radius: 10px;
    transition: 0.3s;
}

.reviews__slider .slick-slide.slick-center {
	opacity: 1;
    z-index: 9;
    box-shadow: 0 16px 43px 0 rgba(#000000, 0.16);

    @include media-breakpoint-up(lg) {
        transform: scale(1.5);
    }
}

.reviews__slider .slick-arrow {
    width: 40px;
    height: 40px;
    border: none;
    cursor: pointer;
    box-shadow: 0 13px 13px 0 rgba(#b5c2b2, 0.26);
    position: relative;
    background-color: $white-color;
    border-radius: 50%;
    z-index: 9;
    position: absolute;

    &:before {
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 35%;
    }

    &:hover, &:focus {
        background-color: $white-color;
    }
}

.reviews__slider .slick-next {
    right: 24%;
    top: 50%;
    transform: translate(0, -100%);

    &:before {
        background-image: url(../img/arrow-left.svg);
        transform: rotate(180deg);
    }
}

.reviews__slider .slick-prev {
    left: 24%;
    top: 50%;
    transform: translate(0, -100%);

    &:before {
        background-image: url(../img/arrow-left.svg);
    }
}

.reviews__slider .slick-slide button{
	display: none;
}

.reviews__slider .slick-slide.slick-current button{
	display: block;
}

.reviews__slider .slick-list {
    overflow: hidden;
    padding: 40px 0 !important;

    @include media-breakpoint-up(md) {
        padding: 108px 0 164px !important;
    }
}

.reviews__tabs {
    text-align: center;
    margin-top: 33px;
    margin-left: auto;
    margin-right: auto;
    // margin-bottom: 20px;
    box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.05);
    width: fit-content;
    border-radius: 43px;
    border-bottom: none;

    @include media-breakpoint-up(md) {
        margin-top: 40px;
    }

}

.reviews__tab {
    width: 130px;
    height: 100%;
}

.reviews__tab a.active {
    background: linear-gradient(0, #ff9900, #ffd89e);
    border-radius: 40px;
    box-shadow: 0 16px 32px 0 rgba(255,153,0, 0.58);
    color: $white-color !important;
    text-shadow: 0px 1px 4px rgba(0,0,0,0.17);
}

.reviews__tab a {
    border: none !important;
    height: 100%;
    line-height: 27px;
    font-size: 14px;
}

.reviews__controls {
    position: relative;
}

.reviews__pagination{
    text-align: center;
    position: absolute;
    bottom: 10px;
    left: calc(50% - 15px);
    display: block;

    @include media-breakpoint-up(md) {
        bottom: 100px;
    }
    @include media-breakpoint-up(lg) {
        display: none;
    }
}

.reviews__pagination span {
    font-family: "Oranienbaum";
    font-size: 30px;
}

.tab-content > .tab-pane {
    display: block;
    height: 0;
    overflow: hidden;
}
.tab-content > .active {
    height: auto;
}
