.video-link {
    position: absolute;
    top: 39%;
    right: 41%;
    display: flex;
    flex-direction: column;
    align-items: center;

    &--courses {
        text-shadow: 0px 1px 4px #000;
        top: 53%;
        right: 24%;

        @media (min-width: 345px){
            top: 54%;
            right: 33%;
        }

        @media (min-width: 395px){
            top: 54%;
            right: 43%;
        }

        @media (min-width: 465px){
            top: 54%;
            right: 52%;
        }

        @media (min-width: 465px){
            top: 54%;
            right: 56%;
        }
        
        @include media-breakpoint-up(sm) {
            top: 46%;
            right: 5%;
        }

        @media (min-width: 620px){
            top: 46%;
            right: -2%;
        }

        @media (min-width: 685px){
            top: 46%;
            right: -8%;
        }

        @media (min-width: 735px){
            top: 46%;
            right: -14%;
        }

        @include media-breakpoint-up(md) {
            top: 45%;
            right: 0%;
        }

        @media (min-width: 800px){
            top: 45%;
            right: -4%;
        }

        @include media-breakpoint-up(lg) {
            top: 42%;
            right: 24%;
        }

        @media (min-width: 1600px){
            top: 44%;
            right: 21%;
        }

        @media (min-width: 1800px){
            top: 44%;
            right: 18%;
        }
    }

    &--about {
        top: 44.3%;
        right: 44.3%;

        @media (min-width: 450px){
            right: 28%;
        }
    
        @media (min-width: 576px){
            right: 17%;
        }
    
        @media (min-width: 610px){
            right: 15%;
        }
    
        @media (min-width: 768px){
            top: 31%;
            right: 18%;
        }
    
        @media (min-width: 992px){
            right: 21%;
            top: 25%;
        }
    
        @media (min-width: 992px){
            right: 18%;
            top: 19%;
        } 
    }
}

@keyframes wave-stroke {
    100% {
        width: 300%;
        height: 300%;
        border-color: transparent;
        opacity: 0
    }
}

.video-link a {
    &:after {
        content: "";
        position: absolute!important;
        left: 50%!important;
        top: 50%!important;
        transform: translate(-50%,-50%)!important;
        border-radius: 50%!important;
        color: #ffd89e;
        border: 2px solid!important;
        width: 100%;
        height: 100%;
        opacity: .9!important;
        box-sizing: border-box!important;
        animation: 2s cubic-bezier(.37,0,.8,.77) infinite wave-stroke;
    }
}

.video-link__description {
    max-width: 150px;
    text-align: center;
    margin-top: 25px;
    color: white;
    font-size: 14px;
    line-height: 25px;

    @include media-breakpoint-up(xl) {
        max-width: 200px;
    }

    &--courses {
        max-width: 240px;
    }
}

.video-link--about .video-link__description{
    margin-top: 8px;
    line-height: 22px;

    @include media-breakpoint-up(md) {
        margin-top: 25px;
        line-height: 25px;
    }
}