.social {
    display: flex;

    @include media-breakpoint-up(md) {
        display: block;
    }
}

.social__icons {
    display: flex;
    margin-bottom: 9px;
    order: 2;
}

.social__item {
    height: 15px;
    width: 15px;
    margin-right: 14px;

    @include media-breakpoint-up(md) {
        height: 20px;
        width: 20px;
        margin-right: 15px;
    }
}

.social__link img {
    max-width: 100%;
}

.social__title {
    padding-left: 2px;
    font-size: 14px;
    color: $grey-color;
    font-weight: 300;
    margin-right: 20px;

    @include media-breakpoint-up(md) {
        padding-left: 0px;
    }
    
}
