.modal-gratitude {
    background: linear-gradient(116deg, #fafcfc,#ffe6c2);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    z-index: 99999;
    height: 100%;
    min-height: 100%;
    -webkit-overflow-scrolling: touch;
    display: none;
}

.modal-gratitude.active {
    display: block;
}

.modal-gratitude__container {
    @include make-container();
    min-height: 80vh;
    margin: 0;
    min-width: 100vw;
    display: flex;
    background-color: #fff;
    background: linear-gradient(116deg, #fafcfc,#ffe6c2);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}


.modal-gratitude__logo {
    position: absolute;
    top: 0;
    left: calc(50% - 105px);
    width: 210px;

}
.modal-gratitude__image {
    width: 130px;
    margin-bottom: 35px;
    padding-top: 100px;
}
.modal-gratitude__title {
    margin-bottom: 30px;
}
.modal-gratitude__subtitle {
    margin-bottom: 50px;
}