.header {
    @include make-container();
    display: flex;
    justify-content: space-between;
    //height: 55px;
    max-width: 1360px;

    @include media-breakpoint-up(lg) {
        padding-top: 45px;
        margin-bottom: 81px;
    }
}

.header__left {
    display: flex;
    align-items: center;
}

.header__right {
    display: flex;
    align-items: center;
}

.header__hamburger {
    width: 21px;
    height: 15px;
    position: relative;
    margin: 0 10px;
    cursor: pointer;
    z-index: 91;
    display: block;

    @include media-breakpoint-up(sm) {
        margin: 0 20px;
    }

    @include media-breakpoint-up(lg) {
        display: none;
    }
}

.header__hamburger.active {
    transform: rotate(90deg);
}

.header__hamburger span {
    width: 100%;
    height: 2px;
    background: $black-color;
    position: absolute;
    top: 0;

    &:nth-child(2) {
        top: 6px;
    }

    &:nth-child(3) {
        top: 12px;
    }
}

.header__logo {
    display: none;
    width: 205px;

    @include media-breakpoint-up(lg) {
        display: inline-block;
        margin-right: 50px;
    }
}

.header__logo img {
    display: block;
    max-width: 100%;
}

.header__card {
    background-color: $white-color;
    background-size: 17px;
    background-position: 15px;
    background-repeat: no-repeat;
    border-radius: 20px;
    box-shadow: 0 3px 21px 0 rgba(#000000, 0.09);
    color: $grey-color;
    display: none;
    padding: 12px 25px 12px 40px;
    font-family: "San Francisco";
    font-weight: 300;
    font-size: 14px;

    &--city {
        background-image: url(../img/maps-and-flags.svg);

        @include media-breakpoint-up(lg) {
            display: block;
        }
    }

    &--phone {
        font-size: 10px;
        display: inline-block;
        background-image: url(../img/call-answer.svg);
        background-size: 15px;

        @include media-breakpoint-up(sm) {
            font-size: 14px;
            padding: 13px 15px 13px 45px;
        }
    }

    &--invisible {
        padding-left: 40px;
        padding-right: 20px;
        height: 32px;
        border: none;
        display: block;
        cursor: pointer;
        font-size: 10px;

        @include media-breakpoint-up(sm) {
            height: 39px;
            font-size: 14px;
        }

        @include media-breakpoint-up(lg) {
            display: none;

        }
    }
}

.header__card strong {

    color: $black-color;
    font-family: "San Francisco", "Arial", sans-serif;
    font-weight: 400;
}

.header__card--city strong {
    display: inline-block;
    margin-left: 8px;
}

.header__text--dotted {
    background-color: transparent;
    border: none;
    color: $black-color;
    cursor: pointer;
    margin: 0 25px;
    position: relative;
    -webkit-appearance: none;
    display: none;

    @include media-breakpoint-up(lg) {
        display: block;
    }

    &::after {
        content: '';
        position: absolute;
        bottom: -3px;
        left: 0;
        width: 101px;
        height: 0;
        border-bottom: 1px dotted #333333;
    }
}

.header__right .header__text--dotted {
    display: block;
    position: absolute;
    top: 50px;
    right: 3px;
    font-size: 10px;

    &:after {
        width: 75px;

        @include media-breakpoint-up(lg) {
            width: 101px;
        }
    }

    @include media-breakpoint-up(lg) {
        position: relative;
        font-size: 14px;
        top: unset;
        right: unset;
    }
}
