.btn {
    background: linear-gradient(0, #ff9900, #ffd89e);
    border: none;
    border-radius: 30px;
    box-shadow: 0 5px 0 0px #ee9308, 0 16px 32px 0 rgba(255,153,0, 0.58);
    color: #fff;
    cursor: pointer;
    font-size: 13px;
    height: 45px;
    text-shadow: 0px 1px 4px rgba(0,0,0,0.17);
    width: 200px;

    @include media-breakpoint-up(md) {
        font-size: 18px;
        width: 290px;
        height: 60px;
    }

    &--pricing {
        box-shadow: 0 16px 32px 0 rgba(255,153,0, 0.58);
        // height: 38px;
        border-radius: 19px;
        // width: 183px;
        border-radius: 50px;
        letter-spacing: 1px;
        width: 175px;
        margin-left: 7px;

        @include media-breakpoint-up(md) {
            padding: 21px 10px;
            width: 100%;
            margin-left: 0;
        }

    }

    &--staff {
        width: 40px;
        height: 40px;
        box-shadow: 0 16px 32px 0 rgba(255,153,0, 0.58);
        position: relative;

        &::before {
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background-size: 20px;
            background-position: 8px;
            background-repeat: no-repeat;
        }
    }

    &--staff-right {
        &::before {
            background-image: url(../img/right-arrow.svg);
            transform: rotate(180deg);
        }

    }

    &--staff-left {
        &::before {
            background-image: url(../img/right-arrow.svg);

        }
    }

    &--tab {
        box-shadow: 0 16px 32px 0 rgba(255,153,0, 0.58);
        font-size: 14px;
    }

    &--tab-not-active {
        box-shadow: none;
        background: none;
        color: $grey-color;

    }

    &--reviews {
        position: absolute;
        top: calc(50% - 25px);
        left: calc(50% - 25px);
        box-shadow: none;

    }

    &--player {
        width: 50px;
        height: 50px;

        border-radius: 50%;

        &::before {
            content: "";
            top: 0;
            left: 2px;
            width: 100%;
            height: 100%;
            position: absolute;
            background-image: url(../img/music-player-play.svg);
            background-repeat: no-repeat;
            background-size: 13px;
            background-position: center;
        }
    }

    &--offer {
        width: 200px;
        height: 45px;

        @include media-breakpoint-up(lg) {
            width: 247px;
        height: 65px;
        }
    }

    &--video {
        box-shadow: 0 16px 32px 0 rgba(255,153,0, 0.58);
        position: relative;
        width: 65px;
        height: 65px;

        @include media-breakpoint-up(xl) {
            width: 85px;
            height: 85px;
        }
        &::before {
            background-size: 20px;
        }
    }

    &--white {
        width: 230px;
        padding: 20px 40px;
        background: $white-color;
        box-shadow: 0 16px 29px 0 rgba(#000000, 0.14);
        color: $black-color;
        font-size: 14px;
    }



}

