.about-company {
    background-image: url(../img/about-bckgr.png);

    background-repeat: no-repeat;

    background-position-x: right;
    background-position-y: 20%;
    padding-top: 43px;


    @include media-breakpoint-up(sm) {
        background-image: url(../img/video.png);
        background-size: 140%;
        background-position: center;

    }
    @include media-breakpoint-up(md) {
        padding-top: 75px;

    }

}

.about-company__title {
    padding: 91px 0 0px 0;
    line-height: 34px;
    margin-bottom: 14px;

    @include media-breakpoint-up(md) {
        line-height: 52px;
        padding: 85px 0 0 0px;
        margin-bottom: 63px;
    }
}

.about-company__container {
    @include make-container();

    display: flex;
    flex-direction: column;

    @include media-breakpoint-up(sm) {
        max-width: $container-sm;
    }

    @include media-breakpoint-up(md) {
        max-width: $container-md;
    }

    @include media-breakpoint-up(lg) {
        max-width: $container-lg;
    }

    @include media-breakpoint-up(xl) {
        max-width: $container-xl;
    }

    position: relative;
}

.pricing__container-wrap {
    max-width: 1000px;
    margin: auto;
}

.about-company__btn {
    width: 206px;
    height: 43px;
    letter-spacing: 1.1px;
    box-shadow: 0 3px 0 0px #ee9308, 0 16px 32px 0 rgba(255, 153, 0, 0.58);
    margin-bottom: 273px;
    align-self: center;

    @include media-breakpoint-up(md) {
        width: 200px;
        height: 45px;
        box-shadow: 0 5px 0 0px #ee9308, 0 16px 32px 0 rgba(255, 153, 0, 0.58);
        margin-bottom: 81px;
        align-self: flex-start;
    }
}
