.unordered-list {
    margin-bottom: 84px;

    &--pricing {
        margin-bottom: 25px;
        @include media-breakpoint-up(md) {
            margin-bottom: 33px;
        }


    }
}

.unordered-list__item {
    margin-bottom: 15px;
    padding-left: 30px;
    font-size: 14px;
    color: $light-grey-color;
    background-image: url(../img/pin.png);
    background-repeat: no-repeat;
    background-position: -4px 0;

    &--pricing{
        line-height: 1.5;
        background-position: -4px -1px;
        padding-left: 31px;
        margin-bottom: 5px;

        @include media-breakpoint-up(md) {
            padding-left: 37px;
            margin-bottom: 15px;
        }

    }
}

.unordered-list__item strong {
    font-size: 16px;
    color: $black-color;
    padding-left: 7px;
}

.unordered-list__item--pricing strong {
    letter-spacing: -1.2px;

    @include media-breakpoint-up(md) {
        letter-spacing: initial;
    }
}

.unordered-list--application {
    width: 100%;
}

.unordered-list--application .unordered-list__item {
    margin-bottom: 21px;
    font-size: 16px;
    line-height: 25px;
    padding-left: 36px;
}
