.title {
    font-family: "Oranienbaum";
    font-size: 24px;
    font-weight: 300;
    line-height: 52px;
    margin: 0px auto 0;
    text-align: center;
    position: relative;
    width: unset;
    padding-top: 80px;

    @include media-breakpoint-up(lg) {
        width: 550px;
        font-size: 42px;
        line-height: 52px;
    }

    @include media-breakpoint-up(xl) {
        width: 550px;
        font-size: 42px;
    }

    &::after{
        background-color: $light-orange-color;
        content: "";
        height: 60px;
        left: 50%;
        position: absolute;
        top: -0px;
        width: 1px;
    }

    &--left {
        text-align: center;
        margin: 80px auto 0;
        max-width: 300px;

        @include media-breakpoint-up(md) {
            text-align: left;
            margin: 0px 0 0;
        }

        @include media-breakpoint-up(md) {
            max-width: 630px;
        }

        &::after{
            left: 50%;

            @include media-breakpoint-up(lg) {
                left: 0;
            }
        }
    }

    &--pricing {

        @include media-breakpoint-up(md) {
            padding-top: 78px;
            // padding-right: 59px;
        }
    }

    &--courses {
        text-align: left;
        width: 100%;
        line-height: 35px;

        @include media-breakpoint-up(md) {
            width: 640px;
            padding-top: 86px;
            line-height: 52px;
        }
    }

    &--results {
        font-size: 23px;
        @include media-breakpoint-up(md) {
            font-size: 42px;
        }
    }
}

.title--staff {
    font-size: 24px;
    line-height: 35px;
    padding-top: 92px;

    @include media-breakpoint-up(md) {
        width: 260px;
        font-size: 24px;
        text-align: left;
        margin-bottom: 60px;
        line-height: 48px;
    }

    @include media-breakpoint-up(xl) {
        font-size: 36px;
        padding-top: 93px;
        margin-bottom: 40px;
    }

    &::after{
        left: 50%;

        @include media-breakpoint-up(md) {
            left: 5px;
        }
    }
}

.title--app {
    @include media-breakpoint-up(lg) {
        width: 800px;
    }
}
