.subtitle {
    font-size: 18px;
    font-weight: 300;
    margin-top: 25px;
    text-align: center;

    &--left {
        max-width: 300px;
        text-align: center;
        margin: 25px auto 0;

        @include media-breakpoint-up(sm) {
            text-align: left;
            max-width: unset;
            margin: 25px 0 0;
        }
    }

    &--pricing {
        margin-top: 3px;
        line-height: 19px;
        padding-right: 0px;
        font-size: 15px;

        @include media-breakpoint-up(md) {
            margin-top: 14px;
            font-size: 18px;
            // padding-right: 59px;
        }

    }

    &--reviews {



            margin-top: 0px;
        font-size: 14px;
        line-height: 20px;

        @include media-breakpoint-up(md) {
            margin-top: 14px;
        }
    }

    &--courses {

        text-align: left;
        font-size: 14px;
        margin-top: 19px;
        line-height: 19px;

        @include media-breakpoint-up(md) {
            margin: 22px 0 0;
            font-size: 18px;
            // text-align: center;
        }

    }

    &--results {
        margin: 0;
        font-size: 14px;
        line-height: 18px;

        @include media-breakpoint-up(md) {
            font-size: 18px;
            margin-top: 16px;
        }
    }
}
