.application {
    padding-bottom: 79px;
    padding-top: 129px;
}

.application__container {
    @include make-container();

    @include media-breakpoint-up(sm) {
        max-width: $container-sm;
    }

    @include media-breakpoint-up(md) {
        max-width: $container-md;
    }

    @include media-breakpoint-up(lg) {
        max-width: $container-lg;
    }

    @include media-breakpoint-up(xl) {
        max-width: $container-xl;
    }
}

.application__content {
    @include make-row();
    justify-content: center;


    @include media-breakpoint-up(md) {
        margin-top: 50px;
    }
}

.application__info {
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    margin-top: 36px;
    align-items: center;


    @include media-breakpoint-up(sm) {
        @include make-col(9);
    }

    @include media-breakpoint-up(md) {
        @include make-col(6);
    }

    @include media-breakpoint-up(lg) {
        @include make-col(4);
    }

    @include media-breakpoint-up(xl) {
        @include make-col(4);
    }

}

.application__info-content {
  max-width: 294px;
  margin-left: auto;
}

.application__image {
    padding: 0 10px;
    display: flex;
    justify-content: center;
    margin-top: 18px;

    @include media-breakpoint-up(sm) {
        @include make-col(9);
    }

    @include media-breakpoint-up(md) {
        @include make-col(6);
    }

    @include media-breakpoint-up(lg) {
        @include make-col(4);
    }

    @include media-breakpoint-up(xl) {
        @include make-col(4);
    }
}

.application__widget {
    padding: 0 10px;
    display: none;
    justify-content: center;

    margin-top: 30px;
    @include media-breakpoint-up(sm) {
        @include make-col(9);
    }

    @include media-breakpoint-up(md) {
        @include make-col(6);
        display: flex;
    }

    @include media-breakpoint-up(lg) {
        @include make-col(4);
    }

    @include media-breakpoint-up(xl) {
        @include make-col(4);
    }
}

.application__title {
    font-size: 20px;
    line-height: 27.5px;
    margin: 3px 0 34px;
    text-align: center;

    @include media-breakpoint-up(md) {
        text-align: left;
    }
}

.application__links {
    display: flex;
    align-items: center;
    // margin-top: 58px;
}

.application__link:first-child {
    margin-right: 10px;
}


.application__link img {
    display: block;
    width: 100%;

}

.application__image img {
    box-shadow: 0 0 59px 0 rgba(#000000, 0.17);
    border-radius: 45px;
}

.application__widget-vk {
  width: 300px;
  margin-left: auto;
}

